@import "https://fonts.googleapis.com/css2?family=Oswald:wght@200..700&family=Playwrite+GB+S:ital,wght@0,100..400;1,100..400&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap";
.elements p {
  color: #fff;
  font-family: Oswald;
  font-size: 2vh;
}

.elements h2 {
  color: #fff;
  margin: 0 0 0 10px;
  font-family: Oswald;
  font-size: 5vh;
}

.elements h3 {
  color: #4d0088;
  background-color: #fff;
  margin: 0;
  font-family: Oswald;
  font-size: 3vh;
}

.elements {
  grid-template-rows: auto auto auto auto;
  grid-template-columns: 10% 1fr 25% 10%;
  gap: 10px;
  height: 100%;
  display: grid;
}

.elements .title {
  background-color: #4d0088;
  grid-row: 1;
  grid-column-start: span 4;
}

.elements .title h2 {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.elements #date {
  grid-area: 2 / 1;
  align-items: center;
  margin-left: 10px;
  display: flex;
}

.elements #time {
  grid-area: 3 / 1;
  align-items: center;
  margin-left: 10px;
  display: flex;
}

.elements #source {
  grid-area: 4 / 1;
  align-items: center;
  margin-left: 10px;
  display: flex;
}

.elements #desc {
  grid-area: 2 / 2 / 4;
  align-items: center;
  display: flex;
}

.elements #img {
  grid-area: 2 / 3 / 5;
  max-width: 100%;
  max-height: 100%;
  margin: auto;
}

.elements #ratings-button {
  grid-column: 4;
  grid-row-start: span 3;
}

.elements #url {
  grid-area: 4 / 2;
  align-items: center;
  max-width: fit-content;
  display: flex;
}

.elements #ratings-button {
  cursor: pointer;
  color: #6900af;
  text-shadow: 0 1px #fff;
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 100px;
  grid-row-start: span 3;
  width: 75%;
  margin: auto;
  font-family: Oswald;
  font-size: 4vh;
  text-decoration: none;
  display: inline-block;
}

.elements #ratings-button:hover {
  background-color: #e2b7ff;
}

.elements #ratings-button:active {
  position: relative;
  top: 1px;
}
